.dashboard-container {
  width: 100%;
  height: 100vh;
  display: grid;
  grid-template-rows: 48px auto;
  grid-template-columns: 376px auto;
}

.dashboard-container__header {
  grid-column: 1 / 3;
  grid-row: 1 / 2;
  display: flex;
  width: 100vw;
}

.dashboard-container__aside {
  grid-column: 1 / 2;
  grid-row: 2 / 3;
  display: flex;
}

.dashboard-container__main {
  grid-row: 2 / 3;
  display: flex;
  margin: 5px;
  display: flex;
  flex-direction: column;
  max-height: calc(100vh - 48px);
}


