  background: url(/carto_marta-vidal/static/media/hashtag.68495132.svg) no-repeat;
}

.sidebar-compact-menu__footer-icons.-question {
  background: url(/carto_marta-vidal/static/media/question.822ad081.svg) no-repeat;
}
.sidebar-compact-menu__footer-icons.-dialog {
  background: url(/carto_marta-vidal/static/media/dialog.024481b9.svg) no-repeat;
}

.sidebar-compact-menu__footer-icons:hover {
  animation: bounce 0.6s ease;
  -webkit-animation: bounce 0.6s ease;
}

/* sidebar_list-menu */

.sidebar_list-menu {
  background-color: #f8f9f9;
  height: 100%;
  width: 100%;
  border: 1px solid lightgrey;
}
.sidebar_list-menu-h2 {
  font-family: var(--font-family-primary);
  font-size: var(--text-base);
  font-weight: 600;
  line-height: 1.5;
  letter-spacing: 0.15px;
  color: var(--text-dark-a-primary);
  margin: 28px 16px;
}
.sidebar_list-menu__file-explorer {
  margin: 2px 12px 2px 12px;
  font-family: var(--font-family-secondary);
  font-size: var(--text-sm);
  font-weight: 600;
  letter-spacing: 0.25px;
  color: var(--text-dark-a-primary);
  display: flex;
  flex-direction: row;
  height: 40px;
  align-items: center;
  transition: background-color 0.3s ease-out;
  border-radius: 5px;
  padding: 0px 8px 0px;
  width: auto;
}

.sidebar_list-menu__file-explorer:hover {
  background-color: rgba(3, 111, 226, 0.08);
  cursor: pointer;
}
.sidebar_list-menu__file-explorer img {
  border-radius: 5px;
}
.sidebar_list-menu__file-explorer span {
  display: flex;
  align-items: center;
  margin-left: 12px;
}

@media (min-width: 320px) and (max-width: 1012px) {
  .sidebar_container {
    display: flex;
    flex-direction: column;
  }
  .sidebar_list-menu-h2 {
    margin: 20px 16px;
  }
  .sidebar-compact-menu {
    justify-content: center;
  }
  .sidebar-compact-menu__top-icons {
    padding: 16px 0px;
  }
  .sidebar-compact-menu,
  .sidebar-compact-menu__top-icons,
  .sidebar-compact-menu__footer-icons_wrapper {
    flex-direction: row;
    flex-wrap: wrap;
  }
  .sidebar-compact-menu__bottom-icons,
  .sidebar-compact-menu__footer-icons_wrapper {
    display: none;
  }
}

