:root {
  --tab--font-family: var(--font-family-primary);
  --tab--type-text__bg: var(--white);
  --tab--type-text__primary-color: var(--blue);
  --tab--type-text__tertiary-color: var(--black);
}

.tabs-container {
  padding: var(--spacing-2) var(--spacing-6);
}
.tabs-container__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
}
.tabs-container__list-element__button-wrapper button {
  min-width: 50px;
  height: 48px;
  margin: 0 24px 0 0;
  padding: 12px 0 0;
  font-family: var(--font-family-primary);
  font-family: var(--tab--font-family);
  font-size: 14px;
  font-weight: 600;
  border: none;
  background-color: var(--white);
  border-bottom: 2px solid var(--white);
  transition: all ease-in-out 0.2s;
  color: var(--primary-a-main);
}
.tabs-container__list-element__button-wrapper button:active,
.tabs-container__list-element__button-wrapper button:focus-within,
:target {
  border-bottom: 4px solid black;
  color: black;
}

.tabs-container__list-element__button-wrapper button:focus-visible {
  outline: none;
}

.tabs-container__list-element__container {
  border-top: 1px solid rgba(44, 48, 50, 0.12);
  max-width: calc(100vw - 440px);
  margin-top: 1px;
  font-family: var(--font-family-secondary);
  font-size: 12px;
  max-height: calc(100vh - 340px);
  overflow-y: scroll;
}

@media (min-width: 320px) and (max-width: 1012px) {
  .tabs-container__list-element__container {
    max-width: inherit;
    max-height: inherit;
  }
  .tabs-container__list-element__container,
  .dashboard-container__main__map,
  .table-container {
    padding: 0px;
  }
  .tabs-container__list-element__container {
    max-height: inherit;
  }
}

.dashboard-container__main__map {
  overflow: hidden;
  max-width: 100%;
}

/* Map */
#map__wrapper {
  display: flex;
  justify-content: normal;
  align-items: center;
  overflow: scroll;
  height: 100%;
  overflow-y: scroll;
  overflow-x: scroll;
  max-width: 100%;
}
.dashboard-map__wrapper {
  display: block;
  background-size: contain;
  padding: 100px 0px 0px;
  background-repeat: no-repeat;
}

#map__wrapper img {
  width: 100%;
  transition: width 0.5s ease-out;
  object-fit: cover;
}

.zoom-button__wrapper {
  width: 110px;
  height: 37px;
  display: flex;
  justify-content: space-between;
  background-color: white;
  border-radius: 26px;
  position: absolute;
  bottom: 50px;
  margin: 0px 0px 0px 30px;
  border-radius: 12px;
  box-shadow: 0 1px 2px 1px rgba(191, 191, 191, 0.8);
}

.zoom-button {
  height: 37px;
  width: 37px;
  background-color: white;
  font-size: var(--text-4xl);
  color: black;
  justify-content: center;
  display: flex;
  align-items: baseline;
  text-decoration: none;
  border: 1px solid #f2f2f2;
}
.zoom-button span {
  margin: -6px;
  font-size: var(--text-3xl);
}

.zoom-button:hover {
  box-shadow: none;
}

#zoom-number {
  align-self: center;
  font-size: 12px;
  font-family: var(--font-family-secondary);
  color: rgba(44, 48, 50, 0.6);
}

@media (min-width: 320px) and (max-width: 1012px) {
  .zoom-button__wrapper {
    display: none;
